import { widget } from "./charting_library"
import { useEffect, useState } from 'react';
import { save_chart_data } from "redux/actions/provider";
import { useDispatch } from "react-redux";

import axios from "axios";
// import {
//   subscribeOnStream,
//   unsubscribeFromStream,
// } from './streaming.js';
import { useSelector } from 'react-redux';

// const lastBarsCache = new Map();
function TradingView() {
  const dispatch = useDispatch();
  const [timeframe, setTimeframe] = useState(15);

  const chartData = useSelector((store) => store.provider.chart)
  const tokenSymbol = useSelector((store) => store.provider.symbol);
  const configurationData = {
    supported_resolutions: ['1', '3', '5', '15', '30', '1h', '2h', '4h', '1D', '1W'],
    exchanges: [{
      value: 'SOLANA',
      name: 'SOLANA',
      desc: 'SOLANA',
    }
    ],
  };
  let Datafeed = {
    onReady: (callback) => {
      setTimeout(() => callback(configurationData));
    },
    resolveSymbol: async (
      symbolName,
      onSymbolResolvedCallback,
      onResolveErrorCallback,
    ) => {
      var symbolData = {
        ticker: `Solana:${tokenSymbol}/USD`,
        name: `${tokenSymbol}/USD`,
        description: `${tokenSymbol}/USD`,
        type: "crypto",
        session: '24x7',
        timezone: 'Etc/UTC',
        exchange: "Solana",
        minmov: 1,
        pricescale: 1000000,
        has_intraday: true,
        has_no_volume: true,
        has_weekly_and_monthly: false,
        supported_resolutions: configurationData.supported_resolutions,
        volume_precision: 2,
        data_status: 'streaming',
      }
      onSymbolResolvedCallback(symbolData);
    },

    getBars: async (symbolInfo, resolution, periodParams, onHistoryCallback, onErrorCallback) => {
      const { from, to } = periodParams;
      try {
        let bars = [];
        chartData.forEach(bar => {
          if (bar.unixTime >= from && bar.unixTime < to) {
            bars = [...bars, {
              time: bar.unixTime * 1000,
              low: bar.l,
              high: bar.h,
              open: bar.o,
              close: bar.c,
            }];
          }
        });


        onHistoryCallback(bars, {
          noData: false,
        });

      } catch (error) {
        onErrorCallback(error);
      }
    },
    subscribeBars: (
      symbolInfo,
      resolution,
      onRealtimeCallback,
      subscribeUID,
      onResetCacheNeededCallback,
    ) => {
      console.log('[subscribeBars]: Method call with subscribeUID:', subscribeUID);
      // subscribeOnStream(
      //   symbolInfo,
      //   resolution,
      //   onRealtimeCallback,
      //   subscribeUID,
      //   onResetCacheNeededCallback,
      //   lastBarsCache.get(symbolInfo.full_name),
      // );
    },

  }

  const getChartData = async (interval) => {
    let address = '';
    let url = window.location.pathname;
    let url_para = window.location.href;
    let url_para_data = new URL(url_para);
    let url_para_address = url_para_data.searchParams.get("token");
    let page = (url.split("/"))[1];
    if (page.toLowerCase() === 'chart') {
      if (url_para_address) {
        address = url_para_address
      }
      else {
        address = '7q3AdgKuMeDRnjaMQs7ppXjaw4HUxjsdyMrrfiSZraiN'
      }
    }
    let day = new Date();
    let startTime = day.setDate(day.getDate() - 500);
    startTime = ((new Date(startTime)).toISOString());
    let endTime = ((new Date()).toISOString());
    startTime = ((new Date(startTime)).getTime()) / 1000;
    endTime = ((new Date(endTime)).getTime()) / 1000;
    console.log(startTime, endTime, "why")
    let chartData = await axios.get(`https://api.solscan.io/amm/ohlcv?address=${address}&type=${interval}&time_from=${startTime}&time_to=${endTime}`);

    if (chartData.data && chartData.data.data && chartData.data.data.items) {
      console.log(chartData.data.data.items, "chart Data")
      dispatch(save_chart_data(chartData.data.data.items));
    }
    else {
      dispatch(save_chart_data([]));
    }
  }
  useEffect(() => {
    // if (chartData) {
    window.tvWidget = new widget({
      symbol: 'Bitfinex:BTC/USD', // default symbol
      interval: timeframe, // default interval
      fullscreen: true, // displays the chart in the fullscreen mode
      container: 'tv_chart_container',
      datafeed: Datafeed,
      library_path: './charting_library/',
      theme: 'Dark'
    });
    // }
    window.tvWidget.onChartReady(() => {
      window.tvWidget.activeChart().onIntervalChanged().subscribe(null, (interval, timeframeObj) => {
        if (interval < 60) {
          getChartData(interval + 'm');
          setTimeframe(interval)
        }
        if (interval >= 60 && interval <= 240) {
          getChartData(interval / 60 + 'H');
          setTimeframe(interval)
        }
        if (interval == "D") {
          getChartData("1" + interval);
          setTimeframe("1" + interval)
        }
        if (interval == "1W") {
          getChartData(interval);
          getChartData(interval)
        }
      });
    });
  }, [chartData, tokenSymbol])

  return (
    <div id="tv_chart_container" ></div>
  );
}

export default TradingView;
